@import "../../globals.scss";
.topbar{
    width: 100%;
    color:$mainColor;
    background-color: white;
    position:fixed;
    top: 0;
    z-index: 3;
    transition: all 1s ease;
    .wrapper{
        padding:10px 30px;
        display: flex;
        align-items:color;
        justify-content:space-between;
        .left{
            display: flex;
            align-items:center;
            .logo{
                
                    
                font-size:40px;
                font-weight: 700;
                text-decoration: none;
                margin-right: 40px;
            }
            .itemContainer{
                display: flex;
                align-items:center;
                margin-left: 30px;
                @include mobile{
                    display: none;
                  }
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }
                span{
                    font-size: 15px;
                    font-weight: 500;
                }
            }
        }
        .right{
            display: flex;
            align-items: center;
            .hamburgur{
                display: flex;
                flex-direction:column;
                justify-content: space-between;
                width:32px;
                height:25px;
                cursor: pointer;
                span{
                    width: 100%;
                    height:3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
    }
    &.active{
        background-color:$mainColor;
        color:white;

        .hamburgur{
            span{   
                &:first-child{
                    background-color:white;
                    transform: rotate(45deg);
                }
                &:nth-child(2){
                    opacity: 0;
                }
                &:last-child{
                    background-color:white;
                    transform: rotate(-45deg);
                }
            }
        }
    }
}